import React, { useState, useRef, useFrame } from 'react';
import * as THREE from 'three'
import { useStore } from '../hooks/useStore';
import {useSpring, animated} from 'react-spring'
import useSound from 'use-sound';
import launchButton from '../images/UI_Launch_Inactive.png'
import playAgainButton from '../images/UI_PlayAgain_Inactive.png'
import loadingButton from '../images/UI_Loading.png'
import bgImg from '../images/splash_background_v3.jpg'
import fgImg from '../images/fgScroll.png'
import champ from '../images/splash_03_champ.png'
import mavsman from '../images/splash_02_mavsman.png'
import logo from '../images/Masketball_Logo-svg.png'
import endScreen from '../images/UI_Results_BG_v4.png'
import thankyou from '../images/UI_Thanks.png'
import instructionsScreen from '../images/UI_Instruction_BG.png'
import instruct1 from '../images/UI_Instruction1.png'
import instruct2 from '../images/UI_Instruction2.png'
import playAgain from '../images/UI_PlayAgain_Active.png'
import southwestWeb from '../images/UI_Southwestern_Active.png'
import takeSelfie from '../images/UI_TakeSelfie_Active.png'
import shareInactive from '../images/UI_Share_Inactive.png'
import launchSound from '../sounds/Launch_Button_1.ogg'
import gMusic from '../sounds/Game_Play_Music_1.ogg'
import mMusic from '../sounds/Menu_Music_Loop_1.ogg'
import loadingBall from '../images/UI_Basketball_Icon.png'
import loadingSpin from '../images/UI_Loading_Icon.png'
import loadingLogo from '../images/UI_LoadingScreen_Logo.png'
import readyButton from '../images/UI_Ready_Inactive.png'
import camButton from '../images/UI_Camera_Inactive.png'

import {takePhoto, share, clearState} from '../facetrack'


const appStates= {
  SPLASH: 'splash',
  DIRECTIONS: 'directions',
  COUNTDOWN: 'countdown',
  PLAYING: 'playing',
  END: 'end',
  PHOTOTAKEN: 'phototaken'
}

export const AppManager = (props) => {
  const [setAppState, appState, setTimerRunning, resetTime, resetPoints, setGaugeRunning, setCtdown, endVisibility, setEndVisibility, points] = useStore((state) => [
    state.setAppState,
    state.appState,
    state.setTimerRunning,
    state.resetTime,
    state.resetPoints,
    state.setGaugeRunning,
    state.setCtdown,
    state.endVisibility,
    state.setEndVisibility,
    state.points,
  ]);

  const sound = new Audio(launchSound);
  // const gameMusic = new Audio(gMusic);
  // const menuMusic = new Audio(mMusic);

  // const [playLaunch, {stopLaunch}] = useSound(launchSound);
  // const [playGameMusic, {stopGameMusic}] = useSound(gMusic, {html5:true});
  // const [playMenuMusic, stopMenuMusic] = useSound(mMusic, {html5:true});

  // React.useEffect(() => {
  //   window.addEventListener('touchstart', () => {
  //     sound.muted = false
  //     sound.play()
  //   })
  // })


  const [mavsIn, setMavsIn] = useState(0);

  const { spring } = useSpring({
    spring: mavsIn,
    config: { mass: 1, tension: 410, friction: 60, precision: 0.001 }
  })

  const mavPositionX = spring.to([0, 1], ['-50%', '50%'])
  const mavPositionY = spring.to([0, 1], ['100%', '50%'])
  const champPositionX = spring.to([0, 1], ['100%', '50%'])
  const champPositionY = spring.to([0, 1], ['-70%', '50%'])

  
  const [selfieMode, setSelfieMode] = useState(false)
  const [photoTaken, setPhotoTaken] = useState(false)

  const [loaded, setLoaded] = useState(false)
  const handleTrackingReady = (event) => {
    setLoaded(true);
  };
  React.useEffect(() => {
    document.addEventListener('trackingReady', handleTrackingReady);
    return () => {
      document.addEventListener('trackingReady', handleTrackingReady);
    };
  }, []);

  const isMounted = useRef(false);

  
  const [launchScreenVis, setLaunchScreenVis] = useState(true)
  const [instructScreenVis, setInstructScreenVis] = useState(true)
  // const [endScreenVis, setEndScreenVis] = useState("hidden")
  // const visref = useRef("visible");
  // const togvis = () => {
  //   console.log("click")
  //   if(test === "hidden"){setTest("visible")}
  //   else {setTest("hidden")}
  // }


  React.useEffect(() => {
    if (isMounted.current) {
      console.log('fetching');
      startIntro();
    } else {
      console.log('prep')
      isMounted.current = true;
    }
  }, [loaded]);

  const startIntro = () => {
    setMavsIn(0);
    setTimeout(function() { 
      setMavsIn(1);
      console.log('now');
    }, 1000);
  }

  const startCountdown = () =>{
    setAppState(appStates.COUNTDOWN);
    setInstructScreenVis(false);
    document.getElementById('webcam').style.zIndex = -1;
    setEndVisibility("hidden");
    resetTime();
    resetPoints();
    setTimeout(function() { 
      setCtdown('3');
      setTimeout(function() { 
        setCtdown('2');
        setTimeout(function() { 
          setCtdown('1');
          setTimeout(function() { 
            setCtdown('');
            startGame();
          }, 1000);
        }, 1000);
      }, 1000);
    }, 200);
  }
  const startGame = () =>{
    setAppState(appStates.PLAYING);
    setTimerRunning(true);
    setGaugeRunning(true);
  }
  const clearPhoto = () =>{
    setPhotoTaken(false);
    clearState();
  }
  return(
    <div>
      {!loaded && <div className="loadingscreen" style={{backgroundImage: 'linear-gradient(to bottom right, black, #004f91)', position: 'absolute', width: '100%', height: '100%', zIndex: 99999,}} >
        <img className="centered" src={loadingLogo} style={{width: '70vw', top: '80px', zIndex: '14'}} alt="splash screen" />
        <img className="centered" src={loadingBall} style={{width: '30%'}} alt="splash screen" />
        <img className="rotateme" src={loadingSpin}  alt="splash screen" />
        <div style={{fontFamily: 'FugazOne-Regular', textAlign: 'center', color: 'white', fontSize: '20px', position: 'absolute', bottom: '35%', left: '50%', transform: 'translate(-50%, 0)'}}>
          LOADING...
        </div>
        {/* <div className="dial-container" style={{ position: 'absolute', top: '50%', left: '50%', transform: 'translate(0%, -50%)'}}>
          <div className="wedge" ></div>
        </div> */}
        {/* <img className="centered button" src={loadingButton} alt="splash screen" /> */}
      </div>}

      {launchScreenVis && <div className="launchscreen containeralt" style={{ zIndex: 12 }}>
        {/* <div >
          <img className="sliding-background" src={bgImg} alt="splash screen" />
        </div> */}
        <div className="sliding-foreground" style={{background: `url(${bgImg})`, backgroundSize: 'contain', backgroundRepeat: 'repeat-x', position: 'absolute', top: '0', left: '0%', width: '400vh', height: '100%', zIndex: '12',}} />
        
        <animated.img className="centered" src={mavsman} style={{left: mavPositionX, top: mavPositionY, transform: 'translate(-65%, -45%)', zIndex: 13}} alt="splash screen" />
        <animated.img className="centered" src={champ} style={{left: champPositionX, top: champPositionY, transform: 'translate(-45%, -20%)', zIndex: 13}} alt="splash screen" />
        {/* <img className="sliding-foreground" style={{position: 'absolute', top: 0}} src={fgImg} alt="splash screen" /> */}
        <img className="centered" src={logo} style={{width: '80vw', top: '90px', zIndex: '14'}} alt="splash screen" />
        <div style={{position: 'absolute', width: '120px', height: '50px', top: '5px', right: '10px', zIndex: '14', overflow: 'hidden'}}>
          <img src={loadingLogo} style={{height: '50px', transform: 'translate(-40%,-10%)'}} alt="splash screen" />
        </div>
        <img className="centered button" src={launchButton} style={{zIndex: '15'}} alt="splash screen" onClick={()=>{ sound.play(); setAppState(appStates.DIRECTIONS); setLaunchScreenVis(false); }}/> {/*playLaunch(); playMenuMusic(); setAppState(appStates.DIRECTIONS); setLaunchScreenVis(false);*/}
        {/* <img className="centered" src={launchButton} style={{zIndex: '15'}} alt="splash screen" /> */}

      </div>}

      {instructScreenVis && <div className="instructions" >
        <img className="centered" src={logo} style={{width: '80vw', top: '90px', zIndex: '14'}} alt="splash screen" />
        <img className="centered button" src={readyButton} alt="splash screen" onClick={()=>{startCountdown(); }}/>
        <img className="centered" src={instructionsScreen} alt="splash screen" />
        <img className="centered" style={{top: '42%', width: '80vw'}} src={instruct1} alt="thank you" />
        <img className="centered" style={{top: '57%', width: '80vw'}} src={instruct2} alt="thank you" />
      </div>}

      <div className="endscreen" style={{visibility: endVisibility}}>
        <img className="centered" src={endScreen} alt="splash screen" />
        <div style={{fontFamily: 'Sarpanch-Black', fontSize: '90px', color: 'white', position: 'absolute', top: '50%', left: '50%',  zIndex: '10'}}>
          <div style={{position: 'absolute', top: '-72vw', left: '-22vw', }} >
            {points}
          </div>
        </div>
        <img className="centered" style={{top: '43%', width: '70vw'}} src={thankyou} alt="thank you" />
        <img className="centered" style={{top: '72%', width: '80vw'}} src={southwestWeb} onClick={()=>{window.location.href = "https://www.utsouthwestern.edu/";}} alt="thank you" />
        <img className="centered" style={{top: '82%', width: '80vw'}} src={playAgain} onClick={()=>{startCountdown(); }} alt="thank you" />
        <img className="centered" style={{top: '92%', width: '80vw'}} src={takeSelfie} onClick={()=>{document.getElementById('webcam').style.zIndex = 11; setSelfieMode(true)}} alt="thank you" />
        <img id="image-preview" className="centered" style={{zIndex: '16'}}></img>

      </div> 

      {selfieMode && <div className="selfie-mode">
        <img src={logo} style={{position: 'absolute', width: '80vw', bottom: '20vw', left: '50%', transform: 'translate(-50%, 0)', zIndex: '14'}} alt="splash screen" />
        <img src={camButton} style={{position: 'absolute', width: '15vw', bottom: '3vw', left: '50%', transform: 'translate(-50%, 0)', zIndex: '14'}} alt="splash screen" onClick={()=>{takePhoto(), setPhotoTaken(true)}} />
      </div>}

      {photoTaken && <div className="photo-taken" style={{backgroundImage: 'linear-gradient(to bottom right, black, #004f91)', position: 'absolute', width: '100%', height: '100%', zIndex: '15',}} >
        <img src={shareInactive} style={{position: 'absolute', width: '60vw', bottom: '5vw', left: '50%', transform: 'translate(-12%, 0)', zIndex: '17'}} onClick={()=>{ share(); }} alt="thank you" />
        <img src={playAgain} style={{position: 'absolute', width: '80vw', bottom: '5vw', left: '50%', transform: 'translate(-75%, 0)', zIndex: '17'}} onClick={()=>{setSelfieMode(false); clearPhoto(); startCountdown(); }} alt="thank you" />
        <img src={takeSelfie} style={{position: 'absolute', width: '80vw', bottom: '22vw', left: '50%', transform: 'translate(-50%, 0)', zIndex: '17'}} onClick={()=>{clearPhoto();}} alt="thank you" />
      </div>}

    </div> 
  )
}