import React, { useState, useEffect } from 'react';
import { useStore } from '../hooks/useStore';
import gauge from '../images/UI_Metter_Container.png'
import top from '../images/UI_Score.png'
import shootBar from '../images/UI_Shoot_RangeBAr.png'

const gaugePositions= {
  LEFT: 'left',
  CENTER: 'center',
  RIGHT: 'right'
}

export const Gauge2 = (props) => {
  const [timeLeft, points, ctdown, setGaugePosition, rapidFire, gaugeRunning,] = useStore((state) => [
    state.timeLeft,
    state.points,
    state.ctdown,
    state.setGaugePosition,
    state.rapidFire,
    state.gaugeRunning,
  ]);

  useEffect(() => {
    let id1,id2, id3, id4;
    
    if(gaugeRunning){
      setGaugePosition(gaugePositions.LEFT);
      setTimeout(function() { 
        setGaugePosition(gaugePositions.CENTER);
        id1 = setInterval(() => {
          setGaugePosition(gaugePositions.CENTER);
        }, 2000);
      }, 400);
      setTimeout(function() { 
        setGaugePosition(gaugePositions.RIGHT);
        id2 = setInterval(() => {
          setGaugePosition(gaugePositions.RIGHT);
        }, 2000);
      }, 600);
      setTimeout(function() { 
        setGaugePosition(gaugePositions.CENTER);
        id3 = setInterval(() => {
          setGaugePosition(gaugePositions.CENTER);
        }, 2000);
      }, 1400);
      setTimeout(function() { 
        setGaugePosition(gaugePositions.LEFT);
        id4 = setInterval(() => {
          setGaugePosition(gaugePositions.LEFT);
        }, 2000);
      }, 1600);
    }
    return () => {
      clearInterval(id1);
      clearInterval(id2);
      clearInterval(id3);
      clearInterval(id4);
    };
  }, [gaugeRunning]);
  

  return(
    <div>
      <div className="score-time" style={{ backgroundImage: `url(${top})`, backgroundSize: '100% 100%', fontFamily:'Barlow-BoldItalic', position: 'absolute', width: '100%', height: '50vw', top: 0, zIndex: 1, transform: 'translate(0%, -50%)'}} >
        <div style={{  color: '#004f91', fontSize: '5vw',  position: 'absolute', bottom: '16%', left: '26%'}} >{timeLeft}</div>
        <div style={{  color: '#43cbe8', fontSize: '4vw',  position: 'absolute', bottom: '19%', left: '65%'}} >{points}</div>
      </div>
      <div className='centered' style={{ fontFamily: 'FugazOne-Regular', textAlign: 'center', color: 'white', fontSize: '40vw', zIndex: 9}}>
        {ctdown}
      </div>
      <img style={{position: 'absolute', width: '108%', left: '-4%', bottom: 0, zIndex: 1, transform: 'translate(0%, 30%)'}} src={gauge} alt="splash screen" />
      {!rapidFire && <img style={{position: 'absolute', width: '108%', left: '-4%', bottom: 0, zIndex: 1, transform: 'translate(0%, 30%)'}} src={shootBar} alt="splash screen" />}
      {gaugeRunning && <div className="wrapper" >
        <div className="dial-container container1" >
          <div className="wedge" ></div>
        </div>
        <div className="dial-container container2" >
          <div className="wedge" ></div>
        </div>
        <div className="marker end"></div>
      </div>}
      {rapidFire && <div className="wrapper2" >
        <div className="dial-container2 container1B" >
          <div className="wedge2" ></div>
        </div>
        <div className="dial-container2 container2B" >
          <div className="wedge2" ></div>
        </div>
      </div>}
    </div>
  )
}                                     