import * as facemesh from "@tensorflow-models/facemesh";
import * as tf from "@tensorflow/tfjs-core";
// import Stats from "stats.js";

let model, ctx, videoWidth, videoHeight, video, canvas;
let browsCt = 0;
let browsAvg = 0;
let browsRoll = [];
let rollSize = 20;
for (let i = 0; i < rollSize; i++) {
  browsRoll[i] = 0;
}
let first = true;


// const outputScale = 1.4; // higher is smaller
const outputZoom = 2.5; // 2 is no zoom, probs dont touch though

const browsRaisedEvent = new Event('browsRaised');
const trackingReadyEvent = new Event('trackingReady');

const destCanvas = document.getElementById("myCanvas");
// destCtx.fillStyle = "#FF0000";
// destCtx.fillRect(0,0, window.innerWidth, window.innerHeight);

// const stats = new Stats();
const state = {
  backend: "webgl",
  maxFaces: 1,
  triangulateMesh: false
};


async function setupCamera() {
  video = document.getElementById("video");
  const stream = await navigator.mediaDevices.getUserMedia({
    audio: false,
    video: {
      facingMode: "user"
    }
  });
  video.srcObject = stream;
  return new Promise((resolve) => {
    video.onloadedmetadata = () => {
      resolve(video);
    };
  });
}
export function takePhoto(){
  const destCtx = destCanvas.getContext("2d");
  destCtx.drawImage(
    video,
    0,
    0,
  );
  destCanvas.toBlob(function(blob){showImagePreview(blob)}, 'image/jpeg', 0.92);
}
async function renderPrediction() {
  // stats.begin();
  const predictions = await model.estimateFaces(video);

  let radius = canvas.width / outputZoom;
  ctx.save();
  ctx.beginPath();
  ctx.arc(canvas.width / 2, canvas.height / 2, radius, 0, 2 * Math.PI);

  ctx.strokeStyle = "#004f91";
  ctx.stroke();
  ctx.clip();

  ctx.drawImage(
    video,
    (videoWidth-canvas.width)/2,
    (videoHeight-canvas.height)/2,
    canvas.width,
    canvas.height,
    0,
    0,
    canvas.width,
    canvas.height
  );
  ctx.restore();
  ctx.stroke();

  //442 258
  if (predictions.length > 0) {
    predictions.forEach((prediction) => {
      const keypoints = prediction.scaledMesh;
      browsRoll[browsCt] = keypoints[257][1] - keypoints[443][1];
      if (browsRoll[browsCt] > browsAvg + 0.5) {
        ctx.fillStyle = "#00ff00";
        if (first) {
          first = false;
          // setTimeout(function(){ first = true }, 1000);
          document.dispatchEvent(browsRaisedEvent);
        }
      } else {
        ctx.fillStyle = "#ffffff"; 
        first= true
      }
      browsCt++;
      if (browsCt >= rollSize) browsCt = 0;
      browsAvg = 0;
      for (var i = 0; i < rollSize; i++) browsAvg += browsRoll[i];
      browsAvg /= rollSize;

      // THIS IS WHERE DRAWING HAPPENS
      // for (let i = 0; i < keypoints.length; i++) {
      //   const x = keypoints[i][0];
      //   const y = keypoints[i][1];
      //   ctx.beginPath();
      //   ctx.arc(x/outputScale, y/outputScale, 1 /* radius */, 0, 2 * Math.PI);
      //   ctx.fill();
      // }
    });
  }
  // stats.end();
  requestAnimationFrame(renderPrediction);
}
export async function main() {
  await tf.setBackend(state.backend);
  // stats.showPanel(0); // 0: fps, 1: ms, 2: mb, 3+: custom
  // document.getElementById("main").appendChild(stats.dom);
  await setupCamera();
  video.play();
  videoWidth = video.videoWidth;
  videoHeight = video.videoHeight;
  video.width = videoWidth;
  video.height = videoHeight;
  destCanvas.width = videoWidth;
  destCanvas.height = videoHeight;
  canvas = document.getElementById("output");
  canvas.width = document.documentElement.clientWidth * .82;//videoWidth/outputScale;
  canvas.height = document.documentElement.clientWidth * .82; //videoHeight/outputScale;
  const canvasContainer = document.querySelector(".canvas-wrapper");
  canvasContainer.style = `width: ${videoWidth}px; height: ${videoHeight}px`;
  canvas.style.zIndex = 1;
  ctx = canvas.getContext("2d");
  ctx.translate(canvas.width, 0);
  ctx.scale(-1, 1);
  ctx.fillStyle = "#32EEDB";
  ctx.strokeStyle = "004f91";
  ctx.lineWidth = 0.5;
  model = await facemesh.load({ maxFaces: state.maxFaces });
  document.dispatchEvent(trackingReadyEvent);
  renderPrediction();
}





