//media-preview section

// const IOS_DOWNLOAD_LOCATION = 'shareddocuments:///private/var/mobile/Library/Mobile Documents/com~apple~CloudDocs/Downloads/'

const getFileNamePrefix = () => 'Masketball'

let currentBlob = null
let currentUrl = null
let currentFilename = null
let imagePreview = document.getElementById("image-preview");

let isiOS, isSamsungBrowser;

export function clearState(){
  currentBlob = null
  if (currentUrl) {
    URL.revokeObjectURL(currentUrl)
  }
  currentUrl = null
  document.getElementById("image-preview").style.visibility = "hidden"
}

export function share(){
  const fileToInclude = new File([currentBlob], currentFilename, {
    type: 'image/jpeg',
    lastModified: Date.now(),
  })

  const shareObject = {
    title: '',
    text: '',
    files: [fileToInclude],
  }

  navigator.share(shareObject)
}

const getTimestamp = () => {
  const now = new Date()
  return [now.getHours(), now.getMinutes(), now.getSeconds()]
    .map(n => n.toString().padStart(2, '0')).join('')
}


const showImagePreview = (blob) => {
  currentBlob = blob
  //ios needs dataURL to share, Samsung Internet needs blobUrl, chrome works with either but blob allows taphold + save dialog
  if(isiOS){
    currentUrl = destCanvas.toDataURL('image/jpeg', 0.92);
  } else{
    currentUrl = URL.createObjectURL(blob);
  }
  currentFilename = `${getFileNamePrefix()}.jpg` //${getTimestamp()}.jpg`
  let imgPreview = document.getElementById("image-preview");
  imgPreview.style.visibility = "visible";
  imgPreview.width = document.documentElement.clientWidth * .8;
  imgPreview.src = currentUrl;
}

const initMediaPreview = (options = {}) => {

  const tmpFile = new File([new Blob()], 'tmp.mp4', {
    type: 'video/mp4',
    lastModified: Date.now(),
  })

  const shareTestObj = {
    files: [tmpFile],
  }

  isiOS = !!navigator.platform.match(/iPhone|iPod|iPad/)
  isSamsungBrowser = !!navigator.userAgent.match(/SamsungBrowser/i)
  console.log("is ios = " + isiOS);
  console.log("is samsung browser = " + isSamsungBrowser);
  //Samsung internet has download bug and needs dedicated button
  // if(isSamsungBrowser){
  //   document.getElementById('samsungBrowserDownloadButton').style.display = 'block';
  // }

}

initMediaPreview()
