import React, { useRef } from "react"
import { useGLTF } from "@react-three/drei"
import { useStore } from '../hooks/useStore';
// import * as textures from '../textures';
// import { TextureLoader } from 'three/src/loaders/TextureLoader.js'

export default function Model(props) {
  const [charTexture] = useStore((state) => [
    state.charTexture
  ]);
  const group = useRef()
  // const { nodes, materials } = useLoader(GLTFLoader, '/mblank_CharStill.glb');
  const { nodes } = useGLTF("./masketball_CharStill.glb");
  // const [bL, id, bR] = useTexture(['/char_blockL.png','/char_idle.png','/char_blockR.png']);
  // const textures=[bL,id,bR];

  return (
    <group ref={group} {...props} dispose={null}>
      <mesh geometry={nodes.char_idle.geometry} position={[3.99, 29.81, 63.85]} >
       <meshStandardMaterial attach="material" map={charTexture} transparent={true}/>
      </mesh>
      {/* <meshMatcapMaterial matcap={texture} attach="material" /> */}
    </group>
  )
}