
import React, { useRef } from "react"
import { useLoader } from "react-three-fiber"
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader"
import { TextureLoader } from 'three';
// import { useGLTF } from "@react-three/drei"
// import { TextureLoader } from 'three/src/loaders/TextureLoader.js'
import scene from '../images/bgStadium.jpg';

export default function Model(props) {
  const group = useRef()
  // const { nodes, materials } = useGLTF("./masketball_Scene.glb");
  const { nodes } = useLoader(GLTFLoader, "./masketball_Scene.glb");
  const bGTex = new TextureLoader().load(scene);

  return (
    <group ref={group} {...props} dispose={null}>
      <mesh geometry={nodes.BG.geometry} position={[-4.28, 46.92, 236]} >
        <meshStandardMaterial attach="material" map={bGTex} />
      </mesh>
    </group>
  )
}