import React from 'react';
import { useStore } from '../hooks/useStore';
import { Text } from '@react-three/drei';
import { useInterval } from '../hooks/useInterval';

const appStates= {
  SPLASH: 'splash',
  DIRECTIONS: 'directions',
  COUNTDOWN: 'countdown',
  PLAYING: 'playing',
  END: 'end',
  PHOTOTAKEN: 'phototaken'
}

export const GameManager = (props) => {
  const [ timeLeft, decTime, points, appState, setAppState, setGaugeRunning, setEndVisibility ] = useStore((state) => [
    state.timeLeft,
    state.decTime,
    state.points,
    state.appState,
    state.setAppState,
    state.setGaugeRunning,
    state.setEndVisibility,
  ]);
  useInterval(
    () => {
      if(appState === appStates.PLAYING){
        decTime();
        if(timeLeft <= 0.1){
          setAppState(appStates.END);
          setEndVisibility("visible");
          setGaugeRunning(false);
        }
      }
    },
    100,
  );
  return null;
  // (
  //   <group position={[.5,3,0]}>
  //     <Text
  //       fontSize={.7}
  //       color="black" // default
  //       anchorX="left" // default
  //       anchorY="bottom" // default
  //       outlineWidth={.05}
  //       outlineColor="#ffffff"
  //     >
  //       {`${timeLeft}\n${points}`}
  //     </Text>
  //   </group>
  // );
}