import React from 'react';
import { useStore } from '../hooks/useStore';
import fire1 from '../images/UI_OrangeBolt_1.png'
import fire2 from '../images/UI_OrangeBolt_2.png'
import {useSpring, animated} from 'react-spring'


export const RFirePrompt = (props) => {
  const [rFirePActive,] = useStore((state) => [
    state.rFirePActive,
  ]);

  const { spring } = useSpring({
    spring: rFirePActive,
    config: { mass: 5, tension: 800, friction: 50, precision: 0.0001 }
  })

  const scale = spring.to([0, 1], ['0%', '120%'])
  const opacity = spring.to([0, 1], [0, 1])

  return(
    <div className="splash">
      <animated.img className='cycle1 centered' src={fire1} alt="Rapid Fire!" style={{opacity: opacity, width: scale}}  />
      <animated.img className='cycle2 centered' src={fire2} alt="Rapid Fire!" style={{opacity: opacity, width: scale}}  />
    </div>
  )
}