import create from 'zustand';
import {char_idle} from '../textures';

const appStates= {
  SPLASH: 'splash',
  DIRECTIONS: 'directions',
  COUNTDOWN: 'countdown',
  PLAYING: 'playing',
  END: 'end',
  PHOTOTAKEN: 'phototaken'
}

const gaugePositions= {
  LEFT: 'left',
  MIDDLE: 'middle',
  RIGHT: 'right'
}

const gameLength = 24;

export const useStore = create((set) => ({
  appState: appStates.SPLASH,
  setAppState: (x) => set((state) => ({ appState: x})),
  timerRunning: false,
  setTimerRunning: (x) => set((state) => ({ timerRunning: x})),
  gaugeRunning: false,
  setGaugeRunning: (x) => set((state) => ({ gaugeRunning: x})),
  gaugeColor: 'hotpink',
  setGaugeColor: (x) => set((state) => ({ gaugeColor: x})),
  rapidFire: false,
  setRapidFire: (x) => set((state) => ({ rapidFire: x})),
  ballReady: true,
  setBallReady: (x) => set((state) => ({ ballReady: x })),
  gaugePosition: gaugePositions.LEFT,
  setGaugePosition: (x) => set((state) => ({ gaugePosition: x})),
  timeLeft: gameLength,
  resetTime: () => set((state) => ({ timeLeft: gameLength})),
  decTime: () => {
    set((state) => ({ timeLeft: (state.timeLeft - .1).toFixed(1)}));
  },
  points: 0,
  resetPoints: () => set((state) => ({points: state.points = 0})),
  inc: () => set((state) => ({ points: state.points + 2})),
  charTexture: char_idle,
  setCharTexture: (x) => set((state) => ({charTexture: x})),
  scorePActive: 0,
  setScorePActive: (x) => set((state) => ({scorePActive: x})),
  rFirePActive: 0,
  setRFirePActive: (x) => set((state) => ({rFirePActive: x})),
  ctdown: "",
  setCtdown: (x) => set((state) => ({ctdown: x})),
  hoopTrig: false,
  setHoopTrig: (x) => set((state) => ({hoopTrig: x})),
  endVisibility: ("hidden"),
  setEndVisibility: (x) => set((state) => ({endVisibility: x})),
}));