import * as THREE from "three"
import React, { useRef, useState, useEffect } from "react"
import { useLoader, useFrame } from "react-three-fiber"
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader"
import { LoopOnce } from "three"
import { useStore } from '../hooks/useStore';
// import { useGLTF } from "@react-three/drei"
// import { TextureLoader } from 'three/src/loaders/TextureLoader.js'

export default function Model(props) {
  const [hoopTrig] = useStore((state) => [
    state.hoopTrig
  ]);
  const group = useRef()
  // const { nodes, materials } = useGLTF("./masketball_Scene.glb");
  const { nodes, materials, animations } = useLoader(GLTFLoader, "./masketball_BasketAnim.glb");
  const actions = useRef()
  const [mixer] = useState(() => new THREE.AnimationMixer())
  useEffect(() => {
    actions.current = { idle: mixer.clipAction(animations[0], group.current) }
    actions.current.idle.setLoop(LoopOnce)
    // actions.current.idle.play()
    return () => animations.forEach((clip) => mixer.uncacheClip(clip))
  }, [])

  useEffect(() => {
    setTimeout(function() { 
      actions.current.idle.reset();
      actions.current.idle.play();
    }, 550);
  }, [hoopTrig])

  useFrame((state, delta) => {
    mixer.update(delta)
  })

  return (
    <group ref={group} {...props} dispose={null}>
      <group position={[0, -34.74, -303.84]}>
        <group position={[0.2, 59.94, 91.6]} rotation={[0, -Math.PI / 2, 0]}>
          <mesh
            geometry={nodes.Supports.geometry}
            material={nodes.Supports.material}
            position={[15.99, 38.02, 0.01]}
            rotation={[0, 0, 0]}
          />
          <mesh
            geometry={nodes.Base.geometry}
            material={nodes.Base.material}
            position={[-34.9, -42.75, 0.01]}
            rotation={[0, 0, 0]}
          />
          <mesh
            geometry={nodes.Arm.geometry}
            material={nodes.Arm.material}
            position={[22.11, 6.62, 0.01]}
            rotation={[0, 0, 0]}
          />
          <mesh
            geometry={nodes.AdSpace2.geometry}
            material={nodes.AdSpace2.material}
            position={[-15.12, 10.07, -0.04]}
            rotation={[0, 0, 0]}
          />
        </group>
        <group position={[0.11, 146.57, 108.71]} rotation={[0, Math.PI / 2, 0]}>
          <mesh
            geometry={nodes.Backboard_Padding.geometry}
            material={nodes.Backboard_Padding.material}
            position={[-1.17, -27.33, 0]}
            rotation={[0, 0, 0]}
          />
          <mesh
            geometry={nodes.BackBoard_Base_1.geometry}
            material={nodes.BackBoard_Base_1.material}
            position={[-0.85, 1.48, 0]}
            rotation={[0, 0, 0]}
          />
          <mesh
            geometry={nodes.BackBoard_Base.geometry}
            material={nodes.BackBoard_Base.material}
            position={[-0.85, 13.84, 0]}
            rotation={[0, 0, 0]}
          />
          <mesh
            geometry={nodes.BackBoard_Glass.geometry}
            material={nodes.BackBoard_Glass.material}
            position={[-2.37, -14.42, 0.13]}
            rotation={[-Math.PI, 0, Math.PI / 2]}
          />
        </group>
      </group>
      <mesh
        name="RimBack"
        geometry={nodes.RimBack.geometry}
        material={nodes.RimBack.material}
        position={[0, 80.25, -190.51]}>
        <mesh
          geometry={nodes.Rim.geometry}
          material={nodes.Rim.material}
          position={[0, 0, 16.13]}
          rotation={[0, Math.PI / 2, 0]}
        />
        <mesh
          geometry={nodes.Net.geometry}
          material={nodes.Net.material}
          position={[0, -10.74, 16.13]}
          rotation={[0, Math.PI / 2, 0]}
        />
      </mesh>
      <mesh geometry={nodes.backboard.geometry} material={nodes.backboard.material} position={[0, 98.2, -192.2]} />
    </group>
  )
}