import React, { useState, useEffect, useRef } from 'react';
import { useStore } from '../hooks/useStore';
import score1 from '../images/UI_BlueBolt_1.png'
import score2 from '../images/UI_BlueBolt_2.png'
import {useSpring, animated} from 'react-spring'


export const ScorePrompt = (props) => {
  const [scorePActive,] = useStore((state) => [
    state.scorePActive,
  ]);

  const { spring } = useSpring({
    spring: scorePActive,
    config: { mass: 5, tension: 800, friction: 50, precision: 0.0001 }
  })

  const scale = spring.to([0, 1], ['0%', '120%'])
  const opacity = spring.to([0, 1], [0, 1])

  return(
    <div className="splash">
      <animated.img className='cycle1 centered' src={score1} alt="score!" style={{ opacity: opacity, width: scale}}  />
      <animated.img className='cycle2 centered' src={score2} alt="score!" style={{ opacity: opacity, width: scale}}  />
    </div>
  )
}